@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
/*========== Colors ==========*/
/*========== Font and typography ==========*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  margin-top: 8rem;
  font-family: "Poppins", sans-serif;
  background-color: #ffffff;
  color: #58555e;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4.5rem;
}

img {
  width: 100%;
  height: auto;
}

.active {
  color: #6923d0;
  font-weight: 400;
}

.show-menu {
  left: 0;
}

.header {
  background-color: #ffffff;
  box-shadow: 0 0.1rem 0.5rem rgba(25, 24, 27, 0.15);
  width: 100%;
  padding: 0 1rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0;
}
.header__toggle {
  display: none;
  height: 7rem;
  position: fixed;
  top: 0;
  right: 1rem;
  transition: all 0.2s;
}
@media (max-width: 500px) {
  .header__toggle {
    display: flex;
  }
}
.header__toggle i {
  width: 2.3rem;
  font-size: 2.25rem;
  align-self: center;
  color: #58555e;
  cursor: pointer;
}
.header__content {
  width: 100%;
  height: 7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0;
}
.header__user-img {
  width: 5rem;
  height: 5rem;
  border-radius: 1000rem;
  transition: 0.1s;
  object-fit: cover;
}
.header__user-img:hover {
  opacity: 0.97;
}

.nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0;
}
.nav__user {
  display: none;
}
.nav__logo {
  opacity: 0;
  flex: 0.85;
}
.nav__logo span {
  margin-left: 0.7rem;
  font-size: 1.4rem;
}
.nav__logo span:hover {
  color: #6923d0;
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  flex: 1;
}
@media (max-width: 500px) {
  .nav__list {
    display: none;
  }
}
.nav__item {
  padding: 0.7rem;
}
.nav__link {
  color: #58555e;
  text-transform: capitalize;
  font-size: 1.58rem;
  font-weight: 300;
  transition: all 0.1s;
}
.nav__link i {
  display: none;
}
.nav__link:hover {
  color: #6923d0;
}
.nav__icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0.27rem;
  flex: 1;
}
@media (max-width: 500px) {
  .nav__icons {
    display: none;
  }
}
.nav__icons i {
  font-size: 2.3rem;
  color: #58555e;
  transition: all 0.2s;
  cursor: pointer;
}
.nav__icons i:nth-child(1):hover {
  transform: scale(1.15);
  color: #f90051;
}
.nav__icons i:nth-child(2):hover {
  transform: scale(1.15);
  color: #00a6ea;
}
.nav__icons i:nth-child(3):hover {
  transform: scale(1.15);
  color: #364a93;
}
.nav__icons i:nth-child(4):hover {
  transform: scale(1.15);
  color: #151b22;
}

.active {
  color: #6923d0;
  font-weight: 400;
}

.show-menu {
  left: 0;
}

@media screen and (max-width: 500px) {
  .nav {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 10;
    background-color: #ffffff;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 10rem 30vh 30vh 1fr;
    width: 35rem;
    height: 100vh;
    box-shadow: 0.5rem 0 2rem rgba(0, 0, 0, 0.15);
    transition: all 0.5s ease;
  }
  .nav__user {
    align-self: flex-end;
    justify-self: center;
    display: block;
    position: relative;
    margin-top: -5rem;
  }
  .nav__user-img {
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    object-fit: cover;
  }
  .nav__user-img:hover {
    transform: scale(1.04) translateY(1px);
  }
  .nav__user-caption {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.173);
    color: #ffffff;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    font-weight: 300;
    opacity: 0;
    transition: 0.3s ease;
    cursor: pointer;
  }
  .nav__user-caption:hover {
    opacity: 1;
  }
  .nav__logo {
    text-align: center;
    padding-right: 1rem;
    opacity: 1;
  }
  .nav__logo span {
    font-size: 1.7rem;
  }
  .nav__logo span:hover {
    color: #6923d0;
  }
  .nav__list {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    flex-direction: column;
    align-self: flex-start;
    margin-top: 2.5rem;
  }
  .nav__link {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.35rem;
  }
  .nav__link i {
    display: block;
  }
  .nav__icons {
    align-self: flex-end;
    justify-self: center;
    padding-bottom: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 0.27rem;
  }
  .nav__icons i {
    font-size: 2.3rem;
  }
}
.home {
  display: grid;
  column-gap: 3rem;
  max-width: 102.4rem;
  height: 100vh;
  margin: 0 auto;
  padding: 0 1rem;
  grid-template-columns: repeat(2, 1fr);
  font-family: inherit;
}
.home__data {
  align-self: center;
  justify-self: center;
  font-family: inherit;
}
.home__name, .home__title {
  font-weight: 600;
  letter-spacing: 2px;
  line-height: 1.5;
}
.home__name {
  color: #6923d0;
}
.home__subtitle {
  font-size: 4.5rem;
  font-weight: 600;
  letter-spacing: 2px;
  margin-top: 1rem;
}
.home__button {
  display: inline-block;
  text-align: center;
  background-color: #6923d0;
  color: #ffffff;
  padding: 1rem 2.5rem;
  border-radius: 5px;
  margin-top: 3rem;
  font-size: 1.5rem;
  transition: all 0.3s ease;
}
.home__button:hover {
  transform: translateY(1px);
  box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.15);
}
.home__photo {
  align-self: center;
  justify-self: center;
  width: 45rem;
  padding-top: 3rem;
}
.home__blob {
  fill: #6923d0;
}
.home__blob-img {
  object-fit: cover;
  width: 48rem;
}

@media screen and (max-width: 768px) {
  .home {
    grid-template-columns: 1fr;
  }
  .home__data {
    margin-bottom: -2rem;
  }
  .home__name {
    color: #6923d0;
  }
  .home__title {
    font-size: 5rem;
  }
  .home__subtitle {
    font-size: 4.3rem;
    font-weight: 600;
  }
  .home__photo {
    align-self: flex-start;
    justify-content: center;
    width: 37rem;
  }
  .home__blob {
    fill: #6923d0;
  }
  .home__blob-img {
    object-fit: cover;
    width: 48rem;
  }
}
@media screen and (max-width: 500px) {
  .home {
    grid-template-columns: 1fr;
  }
  .home__data {
    margin-bottom: -3rem;
  }
  .home__title, .home__subtitle, .home__name {
    font-size: 4rem;
    font-weight: 600;
  }
  .home__photo {
    width: 29rem;
  }
  .home__blob-img {
    width: 48rem;
  }
}
.active {
  color: #6923d0;
  font-weight: 400;
}

.show-menu {
  left: 0;
}

.contact {
  display: grid;
  width: 100%;
  height: 100vh;
  text-align: center;
}
.contact__title {
  align-self: center;
  position: relative;
  font-size: 3.5rem;
  text-align: center;
  margin-top: 7rem;
  margin-bottom: 4rem;
}
.contact__title::after {
  content: "";
  position: absolute;
  top: 4.5rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 40px;
  height: 0.5rem;
  background-color: #58555e;
}
.contact__container {
  align-self: flex-start;
  justify-self: center;
}
.contact__container__form {
  width: 40rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.contact__container__form input,
.contact__container__form textarea {
  font-family: inherit;
  border-radius: 0.5rem;
  padding: 0.7rem 0.7rem;
  border: 1px solid #58555e;
  outline: none;
  font-size: 1.5rem;
}
.contact__container__form input:focus,
.contact__container__form textarea:focus {
  color: #6923d0;
  border: 2px solid #6923d0;
}
.contact__container__form input:focus::placeholder,
.contact__container__form textarea:focus::placeholder {
  color: #6923d0;
}
.contact__container__form button {
  background-color: #6923d0;
  border: none;
  padding: 1rem;
  cursor: pointer;
  margin-bottom: 2rem;
  font-size: 1.7rem;
  color: #f4f0fa;
  border-radius: 0.5rem;
}
.contact__container__form button:hover {
  transform: translateY(1px);
  box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 500px) {
  .contact__title {
    font-size: 3.5rem;
  }
  .contact__container {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  .contact__container__form {
    width: 38rem;
  }
  .contact__container__form input,
.contact__container__form textarea {
    font-size: 1.4rem;
  }
  .contact__container__form button {
    font-size: 1.5rem;
  }
}
.about {
  width: 100%;
  display: grid;
  height: 100vh;
}
.about__container {
  width: 768px;
  margin: 0 auto;
  align-self: flex-start;
  display: grid;
  column-gap: 15rem;
  grid-template-columns: repeat(2, 1fr);
}
.about__title {
  align-self: center;
  position: relative;
  font-size: 3.5rem;
  text-align: center;
  margin-top: 7rem;
}
.about__title::after {
  content: "";
  position: absolute;
  top: 4.5rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 40px;
  height: 0.5rem;
  background-color: #58555e;
}
.about__subtitle {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
.about__info {
  align-self: center;
  justify-self: flex-end;
  font-size: 1.5rem;
  margin-top: -1rem;
  text-align: left;
}
.about__photo {
  margin-top: 1rem;
  width: 25rem;
  height: auto;
  justify-self: flex-end;
  margin-left: 3rem;
  align-self: center;
}
.about__photo img {
  width: 100%;
  object-fit: cover;
  border-radius: 1rem;
}

@media screen and (max-width: 768px) {
  .about__container {
    width: 100%;
    grid-template-columns: 100%;
    row-gap: 5rem;
  }
  .about__info {
    padding: 0.2rem 5rem;
    text-align: center;
  }
  .about__photo {
    justify-self: center;
  }
}
@media screen and (max-width: 500px) {
  .about__info {
    padding: 0.5rem 3rem;
    text-align: center;
  }
  .about__photo {
    width: 22rem;
  }
}
.active {
  color: #6923d0;
  font-weight: 400;
}

.show-menu {
  left: 0;
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1024px;
  display: grid;
  margin-left: 1rem;
  margin-right: 1rem;
}

.skills__container {
  row-gap: 1rem;
  text-align: center;
}
.skills__subtitle {
  font-size: 2rem;
  margin-bottom: 2rem;
}
.skills__title {
  align-self: center;
  position: relative;
  font-size: 3.5rem;
  text-align: center;
  margin-top: 7rem;
  margin-bottom: 4rem;
}
.skills__title::after {
  content: "";
  position: absolute;
  top: 4.5rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 40px;
  height: 0.5rem;
  background-color: #58555e;
}
.skills__text {
  font-size: 1.5rem;
  margin-bottom: 3.5rem;
}
.skills__data {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
  margin-bottom: 2rem;
}
.skills__names {
  display: flex;
  align-items: center;
}
.skills__icon {
  font-size: 3.5rem;
  color: #6923d0;
  margin-right: 0.7rem;
}
.skills__name {
  font-size: 1.3rem;
  font-weight: 600;
}
.skills__bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 95%;
  height: 0.275rem;
  border-radius: 5px;
  background-color: #6923d0;
  z-index: -3;
}
.skills__img {
  border-radius: 5px;
}
.skills__html {
  width: 95%;
}
.skills__css {
  width: 85%;
}
.skills__js {
  width: 65%;
}
.skills__ux {
  width: 85%;
}
.skills__percentage {
  font-size: 1.4rem;
  font-weight: 600;
}

@media screen and (min-width: 280px) {
  .section-title {
    margin-bottom: 4rem;
  }
  .section-title::after {
    top: 3rem;
  }
}
@media screen and (min-width: 576px) {
  .section-title {
    margin-bottom: 4rem;
  }
  .section-title::after {
    top: 3rem;
  }

  .skills__container {
    grid-template-columns: 0.75fr;
    justify-content: center;
  }
}
@media screen and (min-width: 768px) {
  .section-title {
    margin-bottom: 4rem;
  }
  .section-title::after {
    width: 5rem;
    top: 3rem;
  }

  .skills__container {
    margin: 0 auto;
    padding: 0 1rem;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    align-items: center;
    text-align: initial;
  }
  .skills__container div > {
    justify-items: center;
  }
}
@media screen and (min-width: 968px) {
  .section-title {
    margin-bottom: 4rem;
  }
  .section-title::after {
    width: 9rem;
    height: 0.275rem;
    top: 5rem;
  }

  .skills__container {
    padding: 0 1rem;
    column-gap: 3rem;
  }
}
.active {
  color: #6923d0;
  font-weight: 400;
}

.show-menu {
  left: 0;
}